.fg-row {
	display: flex;
	width: 100%;

	&.wrap {
		flex-wrap: wrap; } }

.fg-row-padded {
	@extend .fg-row;

	padding: 0 ($grid-padding / 2);

	&.wrap {
		flex-wrap: wrap; }

	> [class*='fg-col'] {
		padding: 0 ($grid-padding / 2); } }

.fg-col, .fg-col-100 {
	flex: 0 0 100%;
	display: block;
	width: 100%; }

.fg-col-10 {
	@extend .fg-col;

	flex: 0 0 10%;
	max-width: 10%; }

