@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.1/animate.min.css);

.stagger-transition.ng-enter-stagger {
	transition-delay: 50ms;
	-webkit-transition-delay: 50ms;
	transition-duration: 0;
	-webkit-transition-duration: 0; }

.stagger-transition.ng-enter {
	transition: 200ms $ease-leave all;
	-webkit-transition: 200ms $ease-leave all;
	opacity: 0; }

.stagger-transition.ng-enter-active {
	opacity: 1; }

// loader
.loader {
	@extend .flex-xy;

	position: absolute;
	top: 50px;
	right: 0;
	bottom: 0;
	left: 60px;
	background-color: rgba(0, 0, 0, 0.15);
	z-index: 30;

	@include media-sm-up {
		top: 80px;
		bottom: 30px; }


	&.ng-hide-remove {
		animation: 500ms fadeIn; }

	&.ng-hide-add {
		animation: 500ms fadeOut; }

	.sk-cube-grid {
		width: 40px;
		height: 40px;
		margin: 100px auto;

		.sk-cube {
			width: 33%;
			height: 33%;
			background-color: $md-blue;
			float: left;
			-webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
			animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out; }

		.sk-cube1 {
			-webkit-animation-delay: 0.2s;
			animation-delay: 0.2s; }

		.sk-cube2 {
			-webkit-animation-delay: 0.3s;
			animation-delay: 0.3s; }

		.sk-cube3 {
			-webkit-animation-delay: 0.4s;
			animation-delay: 0.4s; }

		.sk-cube4 {
			-webkit-animation-delay: 0.1s;
			animation-delay: 0.1s; }

		.sk-cube5 {
			-webkit-animation-delay: 0.2s;
			animation-delay: 0.2s; }

		.sk-cube6 {
			-webkit-animation-delay: 0.3s;
			animation-delay: 0.3s; }

		.sk-cube7 {
			-webkit-animation-delay: 0s;
			animation-delay: 0s; }

		.sk-cube8 {
			-webkit-animation-delay: 0.1s;
			animation-delay: 0.1s; }

		.sk-cube9 {
			-webkit-animation-delay: 0.2s;
			animation-delay: 0.2s; } } }

@-webkit-keyframes sk-cubeGridScaleDelay {
	0%, 70%, 100% {
		-webkit-transform: scale3D(1, 1, 1);
		transform: scale3D(1, 1, 1); }

	35% {
		-webkit-transform: scale3D(0, 0, 1);
		transform: scale3D(0, 0, 1); } }

@keyframes sk-cubeGridScaleDelay {
	0%, 70%, 100% {
		-webkit-transform: scale3D(1, 1, 1);
		transform: scale3D(1, 1, 1); }

	35% {
		-webkit-transform: scale3D(0, 0, 1);
		transform: scale3D(0, 0, 1); } }
