@mixin media-xs {
	@media screen and (max-width: $sm - 1) {
		@content; } }

@mixin media-sm {
	@media screen and (min-width: $sm) and (max-width: $md - 1) {
		@content; } }

@mixin media-md {
	@media screen and (min-width: $md) and (max-width: $lg - 1) {
		@content; } }

@mixin media-lg {
	@media screen and (min-width: $lg) and (max-width: $xl - 1) {
		@content; } }

@mixin media-xl {
	@media screen and (min-width: $xl) {
		@content; } }

@mixin media-sm-up {
	@media screen and (min-width: $sm) {
		@content; } }

@mixin media-md-up {
	@media screen and (min-width: $md) {
		@content; } }

@mixin media-lg-up {
	@media screen and (min-width: $lg) {
		@content; } }

@mixin media-lg-down {
	@media screen and (max-width: $xl - 1) {
		@content; } }

@mixin media-md-down {
	@media screen and (max-width: $lg - 1) {
		@content; } }

@mixin media-sm-down {
	@media screen and (max-width: $md - 1) {
		@content; } }

@mixin aspect-ratio($width, $height) {
	position: relative;

	&:before {
		display: block;
		content: "";
		width: 100%;
		padding-top: $height / $width * 100%; }

	> .content {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; } }
