html {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-o-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
	font-family: sans-serif;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%; }

*, *:before, *:after {
	-webkit-box-sizing: inherit;
	-moz-box-sizing: inherit;
	-o-box-sizing: inherit;
	-ms-box-sizing: inherit;
	box-sizing: inherit; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, i, u, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, fieldset, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
	font: inherit;
	font-size: 100%; }

ol, ul {
	list-style: none; }

blockquote, q {
	quotes: none; }

blockquote:before, blockquote:after, q:before, q:after {
	content: "";
	content: none; }

[class*='col-'] {
	transition: all 300ms; }

audio:not([controls]) {
	display: none;
	height: 0; }

[hidden], template {
	display: none; }

script {
	display: none !important; }

a {
	text-decoration: none;
	-webkit-user-drag: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-webkit-tap-highlight-color: transparent;

	&[href]:hover {
		cursor: pointer; } }

a, button, :focus, a:focus, button:focus, a:active, a:hover {
	outline: 0; }

body {
	line-height: 1; }

b, strong {
	font-weight: bold; }

dfn {
	font-style: italic; }

code, kbd, pre, samp {
	font-size: 1em;
	font-family: monospace, serif; }

pre {
	white-space: pre-wrap; }

q {
	quotes: "“" "”" "‘" "’"; }

small {
	font-size: 80%; }

sub, sup {
	position: relative;
	vertical-align: baseline;
	font-size: 75%;
	line-height: 0; }

sup {
	top: -0.5em; }

sub {
	bottom: -0.25em; }

fieldset {
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
	border: 1px solid #c0c0c0; }

legend {
	padding: 0;
	border: 0; }

button, input, select, textarea {
	margin: 0;
	font-size: 100%;
	font-family: inherit;
	outline-offset: 0;
	outline-style: none;
	outline-width: 0;
	-webkit-font-smoothing: inherit;
	background-image: none; }

button, input {
	line-height: normal; }

button, html input[type="button"], input[type="reset"], input[type="submit"] {
	cursor: pointer;
	-webkit-appearance: button; }

input[type="search"] {
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
	-webkit-appearance: textfield; }

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none; }

button::-moz-focus-inner, input::-moz-focus-inner {
	padding: 0;
	border: 0; }

textarea {
	overflow: auto;
	vertical-align: top; }

img {
	-webkit-user-drag: none; }

table {
	border-spacing: 0;
	border-collapse: collapse; }
